import React, { useState, useEffect } from 'react'
import QRCode from "react-qr-code";


export const Registrar = () => {

  const obtenerRegistros = () => {
    var datos = localStorage.getItem("registroslogin");
    if(datos){
      return JSON.parse(datos);
    }else{
      return [];
    }
  }


  const [registroslogin, setRegistrosLogin] = useState(obtenerRegistros());

  const [direccion, setDireccion] = useState("");
  const [operacion, setOperacion] = useState("");
  const [moneda, setMoneda] = useState("");
  const [cantidad, setCantidad] = useState("");


  const botonGuardar = (e) => {
    e.preventDefault();
    var miObjeto = { direccion, operacion, moneda, cantidad }
    setRegistrosLogin([...registroslogin, miObjeto]);
    limpiarFormulario();
    Qr();
  }


  useEffect(() => {
    localStorage.setItem("registroslogin", JSON.stringify(registroslogin))
  }, [registroslogin] );



  const limpiarFormulario = () => {
    setDireccion("");
    setOperacion("");
    setMoneda("");
    setCantidad("");
    document.getElementById("miFormulario").reset();
  }

  const Qr = () => {
      
  }


  return (
    

    <div className="bg-light" style={{marginTop:20, padding:20}}>

    <div className="h3">
Pagina de Transacciones de REMESAS     <br/>
      <form id="miFormulario" onSubmit={ botonGuardar } >
      <div className="row" style={{marginTop:20}}>
          <div className="col-6">
            <select className="form-select form-select-lg text-center"  onChange={(e) => setOperacion(e.target.value)}  required  >
              <option value="">Indique tipo de Operacion</option>
              <option value="Compra">Ingreso</option>
              <option value="Venta">Egreso</option>
            </select>
          </div>
        
          <div className="col-6">
            <div style={{ height: "auto", margin: "0 auto", maxWidth: 100, width: "100%" }}>
    <QRCode
    size={256}
    style={{ height: "auto", maxWidth: "100%", width: "100%" }}
    value={"Direccion:  " + direccion + 
    "\nTipo de Operacion:  " + operacion + 
    "\nTipo de Moneda:  " + moneda + 
    "\nCantidad:  " + cantidad}
    viewBox={`50 0 256 256`}
      />
    </div>  
    </div>  

        <div className="row" style={{marginTop:-30}}>
          <div className="col-6">
            <input className="form-control form-control-lg text-center" type="text" placeholder="Direccion de Wallet"  onChange={(e) => setDireccion(e.target.value)}  required  />
          </div>
          </div>
          
        <div className="row" style={{marginTop:20}}>
          <div className="col-6">
            <select className="form-select form-select-lg text-center"  onChange={(e) => setMoneda(e.target.value)}  required  >
              <option value="">Tipo de moneda</option>
              <option value="Lamports">Lamports</option>
              <option value="Solana">Solana</option>
            </select>
          </div>
          <div className="col-6">
            <input className="form-control form-control-lg text-center" type="number" min="1" max="100000000" placeholder="Introduce la cantidad $$"  onChange={(e) => setCantidad(e.target.value)}  required  />
          </div>
        </div>

        
      </div>    


        <div className="row" style={{marginTop:20}}>
          <div className="col">
            <button className="btn btn-primary btn-lg">Completar</button>
          </div>
        </div>        
      </form>
    </div>
            
  </div>




  )
}
